import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Route, Redirect, Switch } from "react-router-dom";

// styles
import "./assets/css/bootstrap.min.css";
import "./assets/scss/paper-kit.scss?v=1.2.0";
import "./assets/demo/demo.css?v=1.2.0";
// pages

import LandingPage from "views/examples/LandingPage.js";
import AboutPage from "views/examples/AboutPage.js";
import SearchPage from "views/examples/SearchPage.js";
import MetricsPage from "views/examples/MetricsPage.js";
// others

ReactDOM.render(
  <BrowserRouter>
    <Switch>
      <Route
        path="/landing-page"
        render={(props) => <LandingPage {...props} />}
      />
      <Route path="/search" render={(props) => <SearchPage {...props} />} />
      <Route path="/about" render={(props) => <AboutPage {...props} />} />
      <Route path="/metrics" render={(props) => <MetricsPage {...props} />} />
      <Redirect to="/landing-page" />
    </Switch>
  </BrowserRouter>,
  document.getElementById("root")
);
